const rusTexts = {  
}

export type I18n = Record<keyof typeof rusTexts | keyof typeof rusTextsOnly, string>

const rus: I18n =
  Object.keys(rusTextsOnly)
    .reduce((acc, key) => ({ ...acc, [key]: key }), rusTexts) as I18n

const getI18n = (lang: 'Rus' | 'Eng' | 'Tur'): I18n => {
  switch (lang) {
    case 'Rus': return rus
    case 'Eng': return i18nEnglish    
    case 'Tur': return i18nTurkish
  }
}

const I18nApp  = {
  getI18n
}

export default I18nApp
